<template>
  <div id="app">
    <div id="nav">
      {{userId}}
      <NavigationBar />
    </div>
    <router-view v-bind:userId="userId" v-on:user-logged-in="updateUser"/>
  </div>
</template>

<script>
  import NavigationBar from "./components/layout/NavigationBar.vue"
  import Parse from 'parse'
  export default {
    name: "app",
    components: {
      NavigationBar,
    },
    data() {
      return {
        userId: ""
      }
    },
    methods: {
      updateUser() {
        Parse.initialize("nanoblogo");
        Parse.serverURL = "https://nanoblogo.herokuapp.com/parse";
        localStorage.removeItem("Parse/nanoblogo/currentUser");
        let waitForUser = setInterval(
          () => {
            let currentUser = localStorage.getItem("Parse/nanoblogo/currentUser");
            console.log(currentUser);
            if (currentUser !== null) {
              this.userId = Parse.User.current().getUsername();
              clearInterval(waitForUser)
            }
          }, 100);
        function stopWaitingForUser() {
          clearInterval(waitForUser)
        }
        setTimeout(stopWaitingForUser, 2000);
      }
    },
    created() {
      this.updateUser()
    }
  }
</script>

